export default "import { Component, For, Show } from 'solid-js';\nimport { useFormContext } from './context';\nimport { TextInput } from '@components';\n\nexport const Step3: Component = () => {\n  const MAX_ITEMS = 3;\n  const { formHandler } = useFormContext();\n  const { formData } = formHandler;\n\n  return (\n    <div class=\"row gy-3\">\n      <div class=\"col-12\">\n        <h3>Contact info:</h3>\n      </div>\n      <For each={formData().step3.contact}>\n        {(_, i) => (\n          <div class=\"col-12\">\n            <div class=\"card\">\n              <div class=\"row gy-2 p-2\">\n                <div class=\"col-6\">\n                  <TextInput\n                    label=\"Email\"\n                    name={`step3.contact.${i()}.email`}\n                    formHandler={formHandler}\n                  />\n                </div>\n                <div class=\"col-6\">\n                  <TextInput\n                    label=\"Phone\"\n                    name={`step3.contact.${i()}.phone`}\n                    formHandler={formHandler}\n                  />\n                </div>\n                <Show when={formData().step3.contact.length > 1}>\n                  <div class=\"col-12\">\n                    <button\n                      class=\"btn btn-danger\"\n                      type=\"button\"\n                      onClick={() =>\n                        formHandler.removeFieldset(i(), 'step3.contact')\n                      }\n                    >\n                      Remove\n                    </button>\n                  </div>\n                </Show>\n              </div>\n            </div>\n          </div>\n        )}\n      </For>\n      <Show when={formData().step3.contact.length < MAX_ITEMS}>\n        <div class=\"col-12 d-flex justify-content-end\">\n          <button\n            type=\"button\"\n            class=\"btn btn-primary\"\n            onClick={() =>\n              formHandler.addFieldset({ basePath: 'step3.contact' })\n            }\n          >\n            Add\n          </button>\n        </div>\n      </Show>\n    </div>\n  );\n};\n"